.progress {
  height: auto;
  background-color: white;
  padding-top: 20px;
}

.progress .progress-line {
  width: 66.7%;
  border-top: 2px solid #E31277;
  display: flex;
  justify-content: space-around;
  padding: 4px;
}

.progress .progress-line span {
  font-size: 16px;
  position: relative;
}

.progress .progress-line span:before {
  content: "";
  display: block;
  width: 8px;
  height: 8px;
  border-radius: 40%;
  margin: auto auto;
  padding-bottom: 3px;
  position: absolute;
  left: 45%;
  top: -36%;
}

.progress .progress-line span.done::before {
  background-color: #E31277;
}

.progress .progress-line span.coming::before {
  border: 1px solid  #E31277;
  background-color: white;
}

